.page {
  text-align: center;
  position: relative;
  min-height: 100vh;
}

.content {
  padding-bottom: 5rem;
}

.title {
  padding: 10px;
}

.new-button {
  position: fixed;
  bottom: 3rem;
  right: 3rem;
  z-index: 1;
}

.new-button > button {
  padding: 20px 25px;
  border-radius: 30%;
}

.accomplishments {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.accomplishment-card {
  margin: 0.5rem;
  width: 20rem;
}

.accomplishment-card .manage-buttons {
  float: right;
}

.date-buttons-with-label {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0;
}

.date-buttons-with-label .selected-date {
  margin: 0 10px;
}

.data-management-buttons {
  position: fixed;
  top: 1rem;
  right: 1rem;
  z-index: 1;
}

.data-text {
  width: 100%;
}

.error-text {
  color: red;
}

.hidden {
  display: none;
}

.credit {
  position: absolute;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  bottom: 0;
}